// Buttons ===================================

// Base styles

.btn{
  border-radius: 2rem;
  box-shadow: $sa-box-shadow;
  &:visited { 
    color: white; 
  }
}

.btn-xs{
  padding: 0 $sard-spacer-6;
  font-size: .75rem;
}

.btn-warning, .btn-light{
  &:visited { 
    color: $text; 
  }
}

.btn-link{
  &:visited { 
    color: $link-color; 
  }
}

.btn-primary{
  background-color: $user-colour;
  border-color: $user-colour;
  color: white!important;
  &:hover { 
    background-color: darken($user-colour, 5%); 
    border-color: darken($user-colour, 5%); 
  }
  &:active {
    background-color: darken($user-colour, 10%);
    border-color: darken($user-colour, 10%);
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 0.2rem rgba($user-colour, .25);
  }
}
.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle{
  background-color: darken($user-colour, 15%);
    border-color: darken($user-colour, 15%);
    box-shadow: 0 0 0 0.2rem rgba($user-colour, .25);
}
.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus{
  background-color: darken($user-colour, 15%);
  border-color: darken($user-colour, 15%);
  box-shadow: 0 0 0 0.2rem rgba($user-colour, .25);
}

.btn-secondary{
  background-color: $user-colour-dark;
  border-color: $user-colour-dark;
  color: white!important;
  &:hover { 
    background-color: darken($user-colour-dark, 5%);
    border-color: darken($user-colour-dark, 5%); 
  }
  &:active {
    background-color: darken($user-colour-dark, 10%);
    border-color: darken($user-colour-dark, 10%);
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 0.2rem rgba($user-colour-dark, .25);
  }
}
.btn-secondary:not(:disabled):not(.disabled).active, .btn-secondary:not(:disabled):not(.disabled):active, .show>.btn-secondary.dropdown-toggle{
  background-color: darken($user-colour-dark, 15%);
  border-color: darken($user-colour-dark, 15%);
  box-shadow: 0 0 0 0.2rem rgba($user-colour-dark, .25);
}
.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus{
  background-color: darken($user-colour-dark, 15%);
  border-color: darken($user-colour-dark, 15%);
  box-shadow: 0 0 0 0.2rem rgba($user-colour-dark, .25);
}

$btn-default-colour: $sa-grey-14;
.btn-default{
  background-color: $btn-default-colour;
  color: white!important;
  &:hover { 
    background-color: darken($btn-default-colour, 5%); 
  }
  &:active {
    background-color: darken($btn-default-colour, 10%);
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 0.2rem rgba($btn-default-colour, .5);
  }
}
.btn-default:not(:disabled):not(.disabled).active, .btn-default:not(:disabled):not(.disabled):active, .show>.btn-default.dropdown-toggle{
  background-color: darken($btn-default-colour, 15%);
    border-color: darken($btn-default-colour, 15%);
}

.btn.btn-link{
  box-shadow: none;
}

// SARD Buttons

.btn-save{
  background-color: $success;
  color: white!important;
  &:hover { 
    background-color: darken($success, 5%); 
  }
  &:active {
    background-color: darken($success, 10%);
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 0.2rem rgba($success, .25);
  }
  &::before{
    font-family: "Font Awesome 5 Free"; 
    content: "\f0c7" ;
    color:white;
    font-size: 1.45rem;
    line-height: 1rem;
    position: relative;
    top: 3.5px;
    left: -5px;
