.btn-notext{
  padding: $sard-spacer-6;
  &::before{
    left: 0;
    right: 0;
  }
  &::after{
    left: 0;
    right: 0;
  }
  &.btn-forward{
    padding: $sard-spacer-6 $sard-spacer-8;
  }
  &.btn-back{
    padding: $sard-spacer-6 $sard-spacer-8;
  }
  &.btn-refresh{
    padding: $sard-spacer-6 $sard-spacer-8;
  }
}

.btn-notext.btn-lg{
  padding:  $sard-spacer-7;
  &::before{
    left: 0;
    right: 0;
  }
  &::after{
    left: 0;
    right: 0;
  }
  &.btn-forward{
   padding:  $sard-spacer-7 $sard-spacer-9;
  }
  &.btn-back{
    padding:  $sard-spacer-7 $sard-spacer-9;
  }
}

.btn-notext.btn-sm{
  padding:  $sard-spacer-4;
  &::before{
    left: 0;
    right: 0;
  }
  &::after{
    left: 0;
    right: 0;
  }
  &.btn-forward{
   padding:  $sard-spacer-4 $sard-spacer-6;
  }
  &.btn-back{
    padding:  $sard-spacer-4 $sard-spacer-6;
  }
}

.btn-notext.btn-xs{
  padding:  0 $sard-spacer-2;
  &::before{
    left: 0;
    right: 0;
  }
  &::after{
    left: 0;
    right: 0;
  }
  &.btn-cancel{
    padding: 0 $sard-spacer-3;
  }
  &.btn-forward{
    padding: 0 $sard-spacer-3;
  }
  &.btn-back{
    padding: 0 $sard-spacer-3;
  }
  &.btn-refresh{
    padding: 0 $sard-spacer-3;
  }
  &.btn-upload{
    padding: 0 $sard-spacer-3;
  }
  &.btn-download{
    padding: 0 $sard-spacer-3;
  }
}

.btn-save{
  padding-left: $sard-spacer-14;
  &.btn-lg{
    padding-left: $sard-spacer-18;
  }
  &.btn-sm{
    padding-left: $sard-spacer-12;
  }
  &.btn-xs{
    padding-left: $sard-spacer-8;
  }
  &.btn-notext{
    padding-left: $sard-spacer-7;
  }
}

// Button Outlines ==============================
.btn.btn-outline-primary{
  color: $user-colour;
  border-color: $user-colour;
  &:hover{
    background-color: $user-colour;
    color: white;
  }
}
.btn-outline-primary:not(:disabled):not(.disabled).active, .btn-outline-primary:not(:disabled):not(.disabled):active, .show>.btn-outline-primary.dropdown-toggle{
  background-color: darken($user-colour, 15%);
  border-color: darken($user-colour, 15%);
}

.btn.btn-outline-secondary{
  color: $user-colour-dark;
  border-color: $user-colour-dark;
  &:hover{
    background-color: $user-colour-dark;
    color: white;
  }
}
.btn-outline-secondary:not(:disabled):not(.disabled).active, .btn-outline-secondary:not(:disabled):not(.disabled):active, .show>.btn-outline-secondary.dropdown-toggle{
  background-color: darken($user-colour-dark, 15%);
  border-color: darken($user-colour-dark, 15%);
}

.btn.btn-outline-default{
  color: $btn-default-colour;
  border-color: $btn-default-colour;
  &:hover{
    background-color: $btn-default-colour;
    color: white;
  }
  &:focus,
  &.focus {
    box-shadow: 0 0 0 0.2rem rgba($btn-default-colour, .5);
  }
}
.btn-outline-default:not(:disabled):not(.disabled).active, .btn-outline-default:not(:disabled):not(.disabled):active, .show>.btn-outline-default.dropdown-toggle{
  background-color: darken($btn-default-colour, 15%);
  border-color: darken($btn-default-colour, 15%);
}

.btn.btn-outline-light{
  color: $sa-grey-06;
  border-color: $sa-grey-06;
  &:hover{
    color: $sa-grey-10;
  }
}


.btn.btn-outline-warning{
  color: $text;
  &:hover{
    color: $text;
  }
}

.btn-outline-primary,
.btn-outline-secondary,
.btn-outline-success,
.btn-outline-danger,
.btn-outline-warning,
.btn-outline-info,
.btn-outline-light,
.btn-outline-dark,
.btn-outline-default{
  border-width: 2px;
}

// Button Groups ================================
.btn-group .btn{
  box-shadow: none;
}
.btn-group{
  border-radius: 2rem;
  box-shadow:         0px 1px 1px 0px rgba(0,0,0,0.2);
}