// Color themes
// https://www.sitepoint.com/dealing-color-schemes-sass/
// https://codepen.io/SitePoint/pen/czixa


// ----
// Sass (v3.3.0.rc.5)
// Compass (v1.0.0.alpha.18)
// ----

/* Theme Colouring */

// ================================================================================================
// Main Mixin - Theme Includes are added at the base of this file =================================
@mixin theme($theme-name, $first-colour,  $second-colour, $third-colour,  $forth-colour) {
  $theme-primary:         $first-colour;
  $theme-primary-dark:                    $second-colour;
  $theme-secondary:                                       $third-colour;
  $theme-secondary-dark:                                                  $forth-colour;


  // ================================================================
  // Theme colouring based on theme-name ============================
  .#{$theme-name} {
    // Scrollbar ==============
    ::-webkit-scrollbar-thumb {
        background-color: $theme-primary;
    }

    // Navbar PRIMARY ===========================

    .navbar-primary {
      background-color: $theme-primary;
    }

    /* change the color of active or hovered links */
    .navbar-primary .nav-item:hover .nav-link {
      background-color: darken($theme-primary, 15%); 
    }
    .navbar-primary .nav-item:focus .nav-link {
      background-color: darken($theme-primary, 10%); 
    }
    .navbar-primary .nav-item:active .nav-link {
      background-color: darken($theme-primary, 20%); 
    }
    // add background-color to active links
    .navbar-primary .nav-item.active .nav-link{ 
      background-color: darken($theme-primary, 20%); 
    }
    .navbar-primary .nav-item.active .nav-link:hover{
      background-color: darken($theme-primary, 25%); 
    }
    .navbar-primary .nav-item.active .nav-link:focus{
      background-color: darken($theme-primary, 30%); 
    }

    // Dropdown Menu
    .navbar-primary .dropdown-menu{
      background-color: darken($theme-primary, 15%);
    }

    .navbar-primary .nav-item.dropdown.show .nav-link.dropdown-toggle{
      background-color: darken($theme-primary, 25%);
    }

    .navbar-primary .dropdown-menu .dropdown-item{
      border-color: $theme-primary;
      &:focus{
        background-color: darken($theme-primary, 25%);
      }
      &:hover{
        background-color: darken($theme-primary, 25%);
      }
      &:active{
        background-color: darken($theme-primary, 30%);
      }
    }


    // Submenu
    .navbar-primary .dropdown-menu .dropdown-item.dropdown-toggle:active,
    .navbar-primary .dropdown-menu .dropdown-item.dropdown-toggle.active{
      background-color: darken($theme-primary, 30%);
    }

    // Searchbar
    .navbar-primary .searchbar-sard input{
      border:2px solid darken($theme-primary, 10%);
      &:hover{
        border-color: darken($theme-primary, 15%);
      }
      &:focus{
        border-color: darken($theme-primary, 25%);
      }
    }
    
    .navbar-primary .searchbar-sard .btn-search{
      &::before{
        color:darken($theme-primary, 15%);
      }
    }
    
    .navbar-primary .searchbar-sard .btn-search:hover{
      &::before{
        color: darken($theme-primary, 20%);
      }
    }
    .navbar-primary .searchbar-sard .btn-search:focus{
      &::before{
        color: darken($theme-primary, 25%);
      }
    }
    
    .navbar-primary .searchbar-sard input:hover + .btn-search{
      &::before{
        color: darken($theme-primary, 20%);
      }
    }
    .navbar-primary .searchbar-sard input:focus + .btn-search{
      &::before{
        color: darken($theme-primary, 25%);
      }
    }
    // Navbar Toggler
    .navbar-primary .navbar-toggler{
      background-color: darken($theme-primary, 20%);
      &:hover{
        background-color: darken($theme-primary, 25%);
      }
      &:focus,
      &:active{
        background-color: darken($theme-primary, 30%);
      }
    }

    .navbar-primary .navbar-toggler .navbar-toggler-icon{
      background-image: none;
      &::before{
        color:white;
      }
    }

    // Navbar SECONDARY =========================

    .navbar-secondary {
      background-color: $theme-secondary;
    }

    /* change the color of active or hovered links */
    .navbar-secondary .nav-item:hover .nav-link {
      background-color: darken($theme-secondary, 10%); 
    }
    .navbar-secondary .nav-item:focus .nav-link {
      background-color: darken($theme-secondary, 5%); 
    }
    .navbar-secondary .nav-item:active .nav-link {
      background-color: darken($theme-secondary, 15%); 
    }
    // add background-color to active links
    .navbar-secondary .nav-item.active .nav-link{ 
      background-color: darken($theme-secondary, 15%); 
    }
    .navbar-secondary .nav-item.active .nav-link:hover{
      background-color: darken($theme-secondary, 20%); 
    }
    .navbar-secondary .nav-item.active .nav-link:focus{
      background-color: darken($theme-secondary, 25%); 
    }

    // Dropdown Menu
    .navbar-secondary .dropdown-menu{
      background-color: darken($theme-secondary, 5%);
    }

    .navbar-secondary .nav-item.dropdown.show .nav-link.dropdown-toggle{
      background-color: darken($theme-secondary, 15%);
    }

    .navbar-secondary .dropdown-menu .dropdown-item{
      border-color: $theme-secondary;
      &:focus{
        background-color: darken($theme-secondary, 15%);
      }
      &:hover{
        background-color: darken($theme-secondary, 15%);
      }
      &:active{
        background-color: darken($theme-secondary, 20%);
      }
    }


    // Submenu
    .navbar-secondary .dropdown-menu .dropdown-item.dropdown-toggle:active,
    .navbar-secondary .dropdown-menu .dropdown-item.dropdown-toggle.active{
      background-color: darken($theme-secondary, 20%);
    }

    // Searchbar
    .navbar-secondary .searchbar-sard input{
      border:2px solid darken($theme-secondary, 10%);
      &:hover{
        border-color: darken($theme-secondary, 15%);
      }
      &:focus{
        border-color: darken($theme-secondary, 25%);
      }
    }
    
    .navbar-secondary .searchbar-sard .btn-search{
      &::before{
        color:darken($theme-secondary, 15%);
      }
    }
    
    .navbar-secondary .searchbar-sard .btn-search:hover{
      &::before{
        color: darken($theme-secondary, 20%);
      }
    }
    .navbar-secondary .searchbar-sard .btn-search:focus{
      &::before{
        color: darken($theme-secondary, 25%);
      }
    }
    
    .navbar-secondary .searchbar-sard input:hover + .btn-search{
      &::before{
        color: darken($theme-secondary, 20%);
      }
    }
    .navbar-secondary .searchbar-sard input:focus + .btn-search{
      &::before{
        color: darken($theme-secondary, 25%);
      }
    }
    // Navbar Toggler
    .navbar-secondary .navbar-toggler{
      background-color: darken($theme-secondary, 20%);
      &:hover{
        background-color: darken($theme-secondary, 25%);
      }
      &:focus,
      &:active{
        background-color: darken($theme-secondary, 30%);
      }
    }

    .navbar-secondary .navbar-toggler .navbar-toggler-icon{
      background-image: none;
      &::before{
        color:white;
      }
    }

    // Small Sizes
      // Primary
  @media (max-width: 991px){
    .navbar-primary .dropdown-submenu .dropdown-menu.show{
      border: 1px solid white;
    }
  
    .navbar-primary .dropdown-submenu .dropdown-menu.show{
      border: 0px ;
      background-color: darken($theme-primary, 10%);
    }
  
    .navbar-primary .dropdown-submenu .dropdown-menu .dropdown-item{
      &:hover{
        background-color: darken($theme-primary, 15%);
      }
      &:focus{
        background-color: darken($theme-primary, 20%);
      }
      &:active{
        background-color: darken($theme-primary, 25%);
      }
    }
    // t
    .navbar-primary .dropdown-submenu .dropdown-menu .dropdown-submenu .dropdown-menu.show{
      border: 0px solid white;
      background-color: darken($theme-primary, 15%);
    }
  
    .navbar-primary .dropdown-submenu .dropdown-menu .dropdown-submenu .dropdown-menu .dropdown-item{
      &:hover{
        background-color: darken($theme-primary, 20%);
      }
      &:focus{
        background-color: darken($theme-primary, 25%);
      }
      &:active{
        background-color: darken($theme-primary, 30%);
      }
    }
  } // End small sizes

    // Secondary
    @media (max-width: 991px){
      .navbar-secondary .dropdown-submenu .dropdown-menu.show{
        border: 1px solid white;
      }
    
      .navbar-secondary .dropdown-submenu .dropdown-menu.show{
        border: 0px ;
        background-color: darken($theme-secondary, 10%);
      }
    
      .navbar-secondary .dropdown-submenu .dropdown-menu .dropdown-item{
        &:hover{
          background-color: darken($theme-secondary, 15%);
        }
        &:focus{
          background-color: darken($theme-secondary, 20%);
        }
        &:active{
          background-color: darken($theme-secondary, 25%);
        }
      }
      // t
      .navbar-secondary .dropdown-submenu .dropdown-menu .dropdown-submenu .dropdown-menu.show{
        border: 0px solid white;
        background-color: darken($theme-secondary, 15%);
      }
    
      .navbar-secondary .dropdown-submenu .dropdown-menu .dropdown-submenu .dropdown-menu .dropdown-item{
        &:hover{
          background-color: darken($theme-secondary, 20%);
        }
        &:focus{
          background-color: darken($theme-secondary, 25%);
        }
        &:active{
          background-color: darken($theme-secondary, 30%);
        }
      }
    } // End small sizes

    // Breadcrumbs ==============================
    .breadcrumb{
      a{
        color: $theme-primary;
        &:visited{
          color: $theme-primary;
        }
        &:focus,
        &:hover{
          color: $theme-primary-dark;
        } 
        &:active{
          color: $theme-secondary-dark;
        }
      }
    }

    // Perforations
    hr.primary{ border-color: $theme-primary; }
    hr.secondary{ border-color: $theme-secondary; }

    .perf.primary { background-color: $theme-primary; }
    .perf.secondary { background-color: $theme-secondary; }

    // Trust Colours ==============
    // Background
    .bg-trust{ background-color: $theme-primary !important;}
    .bg-trust-dark{ background-color: darken($theme-primary, 10%) !important;}
    .bg-trust-light{ background-color: lighten($theme-primary, 10%) !important;}

    .bg-trust-2{ background-color: $theme-secondary !important;}
    .bg-trust-2-dark{ background-color: darken($theme-secondary, 10%) !important;}
    .bg-trust-2-light{ background-color: lighten($theme-secondary, 10%) !important;}

    .bg-trust-user{ background-color: $user-colour !important;}
    .bg-trust-user-dark{ background-color: darken($user-colour, 10%) !important;}
    .bg-trust-user-light{ background-color: lighten($user-colour, 5%) !important;}

    .bg-trust-user-2{ background-color: $user-colour-2 !important;}
    .bg-trust-user-2-dark{ background-color: darken($user-colour-2, 5%) !important;}
    .bg-trust-user-2-light{ background-color: lighten($user-colour-2, 10%) !important;}

    .bg-trust-admin{ background-color: $admin-colour !important;}
    .bg-trust-admin-dark{ background-color: darken($admin-colour, 10%) !important;}
    .bg-trust-admin-light{ background-color: lighten($admin-colour, 10%) !important;}

    .bg-trust-admin-2{ background-color: $admin-colour-2 !important;}
    .bg-trust-admin-2-dark{ background-color: darken($admin-colour-2, 10%) !important;}
    .bg-trust-admin-2-light{ background-color: lighten($admin-colour-2, 10%) !important;}

    // Foreground
    .text-trust{ color: $theme-primary !important;}
    .text-trust-dark{ color: darken($theme-primary, 10%) !important;}
    .text-trust-light{ color: lighten($theme-primary, 10%) !important;}

    .text-trust-2{ color: $theme-secondary !important;}
    .text-trust-2-dark{ color: darken($theme-secondary, 10%) !important;}
    .text-trust-2-light{ color: lighten($theme-secondary, 10%) !important;}

    .text-trust-user{ color: $user-colour !important;}
    .text-trust-user-dark{ color: darken($user-colour, 10%) !important;}
    .text-trust-user-light{ color: lighten($user-colour, 5%) !important;}

    .text-trust-user-2{ color: $user-colour-2 !important;}
    .text-trust-user-2-dark{ color: darken($user-colour-2, 5%) !important;}
    .text-trust-user-2-light{ color: lighten($user-colour-2, 10%) !important;}

    .text-trust-admin{ color: $admin-colour !important;}
    .text-trust-admin-dark{ color: darken($admin-colour, 10%) !important;}
    .text-trust-admin-light{ color: lighten($admin-colour, 10%) !important;}

    .text-trust-admin-2{ color: $admin-colour-2 !important;}
    .text-trust-admin-2-dark{ color: darken($admin-colour-2, 10%) !important;}
    .text-trust-admin-2-light{ color: lighten($admin-colour-2, 10%) !important;}

    // Badges ---------------
    .badge.badge-primary{ 
      background-color: $theme-primary; 
    }
    .badge.badge-secondary{ 
      background-color: $theme-secondary; 
    }
    // Buttons --------------
    .btn-primary{
      background-color: $theme-primary;
      border-color: $theme-primary;
      color: white!important;
      &:hover { 
        background-color: darken($theme-primary, 5%); 
        border-color: darken($theme-primary, 5%); 
      }
      &:focus,
      &.focus {
        box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
      }
      &:active {
        background-color: darken($theme-primary, 10%);
        border-color: darken($theme-primary, 10%);
        box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
      }
    }
    .btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle{
      background-color: darken($theme-primary, 15%);
      border-color: darken($theme-primary, 15%);
      box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
    }
    .btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-primary.dropdown-toggle:focus{
      background-color: darken($theme-primary, 15%);
      border-color: darken($theme-primary, 15%);
      box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
    }
    
    
    .btn-secondary{
      background-color: $theme-secondary;
      border-color: $theme-secondary;
      color: white!important;
      &:hover { 
        background-color: darken($theme-secondary, 5%);
        border-color: darken($theme-secondary, 5%); 
      }
      &:active {
        background-color: darken($theme-secondary, 10%);
        border-color: darken($theme-secondary, 10%);
        box-shadow: 0 0 0 0.2rem rgba($theme-secondary, .25);
      }
      &:focus,
      &.focus {
        box-shadow: 0 0 0 0.2rem rgba($theme-secondary, .25);
      }
    }
    .btn-secondary:not(:disabled):not(.disabled).active, .btn-secondary:not(:disabled):not(.disabled):active, .show>.btn-secondary.dropdown-toggle{
      background-color: darken($theme-secondary, 15%);
        border-color: darken($theme-secondary, 15%);
        box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
    }
    .btn-secondary:not(:disabled):not(.disabled).active:focus, .btn-secondary:not(:disabled):not(.disabled):active:focus, .show>.btn-secondary.dropdown-toggle:focus{
      background-color: darken($theme-secondary, 15%);
      border-color: darken($theme-secondary, 15%);
      box-shadow: 0 0 0 0.2rem rgba($theme-secondary, .25);
    }

    .btn.btn-outline-primary{
      color: $theme-primary;
      border-color: $theme-primary;
      &:hover{
        background-color: $theme-primary;
        color: white;
      }
    }
    .btn-outline-primary:not(:disabled):not(.disabled).active, .btn-outline-primary:not(:disabled):not(.disabled):active, .show>.btn-outline-primary.dropdown-toggle{
      background-color: darken($theme-primary, 15%);
      border-color: darken($theme-primary, 15%);
    }

    .btn-outline-primary.focus, .btn-outline-primary:focus{
      box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
    }
    .btn-outline-primary:not(:disabled):not(.disabled).active:focus, .btn-outline-primary:not(:disabled):not(.disabled):active:focus, .show>.btn-outline-primary.dropdown-toggle:focus{
      box-shadow: 0 0 0 0.2rem rgba($theme-primary, .25);
    }
    
    .btn.btn-outline-secondary{
      color: $theme-secondary;
      border-color: $theme-secondary;
      &:hover{
        background-color: $theme-secondary;
        color: white;
      }
    }
    .btn-outline-secondary:not(:disabled):not(.disabled).active, .btn-outline-secondary:not(:disabled):not(.disabled):active, .show>.btn-outline-secondary.dropdown-toggle{
      background-color: darken($theme-secondary, 15%);
      border-color: darken($theme-secondary, 15%);
    }

    .btn-outline-secondary.focus, .btn-outline-secondary:focus{
      box-shadow: 0 0 0 0.2rem rgba($theme-secondary, .25);
    }
    .btn-outline-secondary:not(:disabled):not(.disabled).active:focus, .btn-outline-secondary:not(:disabled):not(.disabled):active:focus, .show>.btn-outline-secondary.dropdown-toggle:focus{
      box-shadow: 0 0 0 0.2rem rgba($theme-secondary, .25);
    }

    // Range Inputs ---------
    input[type=range]::-webkit-slider-thumb {
      background-color: $theme-primary;
    }
    input[type=range]::-moz-range-thumb {
      background-color: $theme-primary;
    }
    input[type=range]::-ms-thumb {
      background-color: $theme-primary;
    }

    // Checkboxes etc -------
    .sa-checkbox input[type="checkbox"]:checked + label span.checkbox::before {
      background-color: $theme-primary;
      border-color: $theme-primary;
    }
    .sa-checkbox-after input[type="checkbox"]:checked + label span.checkbox::after {
      background-color: $theme-primary;
      border-color: $theme-primary;
    }

    // Hover effect
    .sa-checkbox:hover span.checkbox::before,
    .sa-checkbox-after:hover span.checkbox::after{
      border-color: $theme-primary;
    }

    // Radios ---------------
    .sa-radio-buttons input[type="radio"]:checked + .radio-button:after {
      background-color: $theme-primary;
      border-color: $theme-primary;
    }
    .sa-radio-buttons input[type="radio"]:disabled:checked + .radio-button:after{
      border-color: mix($checkbox-background-disabled, $theme-primary, 50%);
      background-color: mix($checkbox-background-disabled, $theme-primary, 50%);
    }
    .sa-radio-buttons-after input[type="radio"]:checked + .radio-button:after {
      color:#0B8;
      background-color: $theme-primary;
      border-color: $theme-primary;
    }
    .sa-radio-buttons-after input[type="radio"]:disabled:checked + .radio-button:after{
      border-color: mix($checkbox-background-disabled, $theme-primary, 50%);
      background-color: mix($checkbox-background-disabled, $theme-primary, 50%);
      cursor: not-allowed;
    }
    // Hover effect
    .sa-radio-buttons:hover .radio-button:after,
    .sa-radio-buttons-after:hover .radio-button:after{
      border-color: $theme-primary;
    }

    // Toggles --------------
    .sa-toggle-switch:hover span.slider::before{
      border-color: $theme-primary;
    }
    .sa-toggle-switch input[type="radio"]:checked + label span.slider::before{
      border-color: $theme-primary;
    }
    .sa-toggle-switch input[type="checkbox"]:checked + label span.slider::after {
      background-color: $theme-primary;
    }
    .sa-toggle-switch-right:hover span.slider::before{
      border-color: $theme-primary;
    }
    .sa-toggle-switch-right input[type="radio"]:checked + label span.slider::before{
      border-color: $theme-primary;
    }
    .sa-toggle-switch-right input[type="checkbox"]:checked + label span.slider::after {
      background-color: $theme-primary;
    }
    .sa-radio-toggle:hover span.radio::before{
      border-color: $theme-primary;
    }
    .sa-radio-toggle input[type="radio"]:checked + label span.radio::before{
      border-color: $theme-primary;
    }
    .sa-radio-toggle input[type="radio"]:checked + label span.radio::after {
      background-color: $theme-primary;
    }
    .sa-radio-toggle-right:hover span.radio::before{
      border-color: $theme-primary;
    }
    .sa-radio-toggle-right input[type="radio"]:checked + label span.radio::before{
      border-color: $theme-primary;
    }
    .sa-radio-toggle-right input[type="radio"]:checked + label span.radio::after {
      background-color: $theme-primary;
    }
    .toggle-check-text:hover{
      border-color: $theme-primary;
    }
    .toggle-check-input:checked ~ .toggle-check-text {
      background: $theme-primary;
      border-color: $theme-primary;
    }
    .toggle-check-text:hover{
      border-color: $theme-primary;
    }
    .toggle-check-input:checked ~ .toggle-check-text {
      background: $theme-primary;
      border-color: $theme-primary;
    }

    // File uploads ---------
    .input-group.sa-file-upload{
      & input.form-control{
        border-color: $theme-primary;
      }
      & .btn{ 
        background-color: $theme-primary;
        border-color: $theme-primary;
        &:hover { 
          background-color: darken($theme-primary, 5%); 
          border-color: darken($theme-primary, 5%); 
        }
        &:active {
          background-color: darken($theme-primary, 10%);
          border-color: darken($theme-primary, 10%);
        }
      }
    }

    // Input Groups ---------
    .input-group>.input-group-prepend>.input-group-text{
      background-color: $theme-primary;
      border-color: $theme-primary;
    }
    .input-group>.input-group-append>.input-group-text{
      background-color: $theme-primary;
      border-color: $theme-primary;
    }
    .input-group > .form-control,
    .input-group > .custom-select,
    .input-group > .custom-file-label{
      &:focus{
        border-color: $theme-primary;
      }
    }

    // BS File Imputs and Uploads ---------------
    .input-group>.custom-file .custom-file-input:focus~.custom-file-label, .input-group>.custom-select:focus, .input-group>.form-control:focus{
      border-color: darken($theme-primary, 10%);
    }

    .custom-file-label{
      border-color: $theme-primary;
    }
    .custom-file-label::after{
      background-color: $theme-primary;
    }
    .custom-file-input~.custom-file-label::after{
      background-color: $theme-primary;
    }
    .custom-file-input:hover~.custom-file-label::after{
      background-color: darken($theme-primary, 5%);
      border-color: darken($theme-primary, 5%);
    }
    .custom-file-input:focus~.custom-file-label::after{
      background-color: darken($theme-primary, 10%);
      border-color: darken($theme-primary, 10%);
    }

    // List Groups ----------
    a.list-group-item-trust{
      color: darken($theme-primary, 0%);
      border-color: darken($theme-primary, 0%);
      &:visited{
        color: darken($theme-primary, 0%);
      }
      &:focus,
      &:hover{
        color: darken($theme-primary, 20%);
        background-color: $sa-grey-04;
      } 
      &:active{
        color: darken($theme-primary, 20%);
        background-color: $sa-grey-04;
      }
      &.active{
        color: white;
        background-color: darken($theme-primary, 0%);
      }
      & small.text-muted{ color: $sa-grey-10!important;}
    }

    // List styles ----------
    ul.trust-list{
      li::before{ color: $theme-primary; }
    }
    ul{
      li.trust-list::before{ color: $theme-primary; }
    }
    
    // Modal Trust ----------------------------------

    .modal-sard .modal-header {
      background-color: $theme-primary;
    }

    .modal-sard .modal-footer {
      background-color: $theme-primary;
    }

    .modal-sard .modal-body a:hover {color: darken($theme-primary, 20%);}
    .modal-sard .modal-body a:focus {color: darken($theme-primary, 25%)!important;}
    .modal-sard .modal-body a:active {color: darken($theme-primary, 25%)!important;}

    .modal-sard .modal-body hr{
      border-color: $theme-primary;
    }


    // Navs -------------------------------------
    .nav-trust.nav.nav-pills .nav-item,
    .nav-trust.nav.nav-pills .nav-link{
      & a{
        color: $theme-primary;
      }
    }
    .nav-trust.nav.nav-pills .nav-link.active{
      background-color: $theme-primary;
      color: white;
      &:focus,
      &:active{
        background-color: $theme-primary;
      }
    }

    // Pagination --------------------------------
    .pagination-sard .page-item.active .page-link{
      background-color: darken($theme-primary, 0%);
      border-color: darken($theme-primary, 0%);
      &:hover{
        background-color: darken($theme-primary, 3%);
        border-color: darken($theme-primary, 3%);
      }
      &:focus{
        background-color: darken($theme-primary, 6%);
        border-color: darken($theme-primary, 6%);
      }
    }
    
    .pagination-sard .page-link{
      color: darken($theme-primary, 0%);
      &:hover{
        background-color: darken($theme-primary, 3%);
        border-color: darken($theme-primary, 3%);
        color: white;
      }
      &:focus{
        background-color: darken($theme-primary, 6%);
        border-color: darken($theme-primary, 6%);
        color: white;
      }
    }

    .pagination-sard{
      .page-link[aria-label="Previous"],
      .page-link[aria-label="Next"] {
        &::before{
          color: $theme-primary;
          }
          &:hover::before,
          &:focus::before{
            color: white;
          }
      } 
    }
    .card-body .pagination-div,
    .panel-body .pagination-div,
    .form-group .pagination-div{
      border-top: 1px solid $theme-primary;
    }

  } // End Theme Name ===============================================
  // ================================================================


} // End Main Mixin ===============================================================================
