// This need to come at the base - Colours for each theme.
//             theme          first-colour      second-colour           third-colour            fourth-colour
@include theme(theme-user,    $user-colour,     $user-colour-dark,      $user-colour-2,         $user-colour-2-dark  );
@include theme(theme-admin,   $admin-colour,    $admin-colour-dark,     $admin-colour-2,        $admin-colour-2-dark   );

@include theme(theme-trust,   $trust-colour,    $trust-colour-dark,     $trust-colour-2,         $trust-colour-2-dark );



